<template>
  <div v-if="letter"
       class="letter-detail-wrapper">
    <div class="letter-detail"
         :class="{'dialog-mode': dialogMode}">
      <img src="../../images/pen.png"
           alt="">
      <div v-html="highlightBody(letter)"></div>
      <div class="attachments"
           v-if="attachments">
        <grid-view :numColumns="attachments.length > 2 ? 3 : 2"
                   :spaceX="10"
                   :spaceY="10">
          <a :key="url"
             :href="url"
             target="_blank"
             v-lazy:background-image="url"
             v-for="url in attachments"></a>
        </grid-view>
      </div>
    </div>
    <div class="letter-info"
         v-if="letter">
      <div><span class="title-label">{{$t("word_count")}}</span>{{wordCount}}</div>
      <div><span class="title-label">{{$t("sender_name")}}</span>{{letter.name}}</div>
      <div><span class="title-label">{{$t("send_time")}}</span>{{formatTime(letter.created_at)}}</div>
      <div><span class="title-label">{{$t("arrive_time")}}</span>{{formatTime(letter.deliver_at)}}</div>
      <div v-if="letter.read_at"><span class="title-label">{{$t("read_time")}}</span>{{formatTime(letter.read_at)}}</div>
    </div>
  </div>
</template>
<style lang="stylus">
.letter-detail
  .highlight
    color red
    font-weight bold
</style>

<style lang="stylus" scoped>
@require ('../var.styl')
.letter-detail-wrapper
  width 100%
  max-width 500px
  margin 0 auto
  overflow hidden
.letter-detail
  white-space pre-line
  width 100%
  background white
  padding 40px 20px 20px 20px
  line-height 26px
  font-size $font-letter
  box-sizing border-box
  border-radius 6px
  border 1px solid #eaeaea
  img
    width 100px
    float right
  > div
    clear both
    padding-top 10px
  .attachments a
    background-size cover
    background-repeat no-repeat
.letter-info
  font-size $font-tiny
  margin-top 10px
  color #666
  padding-right 10px
  float right
  .title-label
    display inline-block
    width 200px
    text-align right
    margin-right 10px
</style>
<script>
import { mapState, mapMutations } from "vuex"
import * as util from "../util"
import * as api from "../api"
import GridView from "./common/GridView.vue"
export default {
  components: {
    GridView
  },
  props: {
    letter: {
      type: Object,
      required: true
    },
    dialogMode: {
      type: Boolean,
      default() {
        return false
      }
    }
  },
  computed: {
    ...mapState(["checkedFriend", "searchValue"]),
    attachments() {
      let l = this.letter
      return l
        ? l.attachments
          ? l.attachments.split(",").map(name => api.buildAttachmentUrl(name))
          : null
        : null
    },
    wordCount() {
      return util.countWords(this.letter.body)
    }
  },
  methods: {
    formatTime(time) {
      return util.offsetAndFormatDate(time)
    },
    highlightBody(letter) {
      let body = (letter.body && letter.body.trim()) || ""
      return this.searchValue
        ? body
            .split(this.searchValue)
            .join('<span class="highlight">' + this.searchValue + "</span>")
        : body
    }
  }
}
</script>

